import React from 'react'
import ReactDOM from 'react-dom'
import json5 from 'json5'
import {
  UnControlled as UncontrolledCodeMirror,
  Controlled as ControlledCodeMirror
} from 'react-codemirror2'
import compileSchema from '../src/compileSchema'
import 'codemirror-graphql/mode'
import 'codemirror/mode/javascript/javascript'

const sdl = `
union BlockContent = Block | Image

type Post {
  title: String!
  slug: Slug
  author: Author
  mainImage: Image @hotspot
  categories: [Category]
  publishedAt: Datetime
  body: [BlockContent]
}

type Category {
  title: String!
  description: Text
}

type Author {
  name: String!
  slug: Slug
  image: Image @hotspot
  bio: [Block]
}
`

function getCompiled(value) {
  let schema
  try {
    const compiled = compileSchema(value, {externalTypes: []})
    schema = json5.stringify(compiled, {space: 2, quote: "'"})
  } catch (err) {
    schema = err.message
  }
  return schema
}

class SanityGraphQLDemo extends React.Component {
  constructor(props) {
    super(props)
    this.state = {schema: getCompiled(sdl)}
    this.handleChange = this.handleChange.bind(this)
  }

  handleChange(editor, data, value) {
    const schema = getCompiled(value)
    this.setState({schema})
  }

  render() {
    return (
      <React.Fragment>
        <UncontrolledCodeMirror
          value={sdl}
          options={{
            mode: 'graphql',
            theme: 'material'
          }}
          onChange={this.handleChange}
        />
        <ControlledCodeMirror
          value={this.state.schema}
          options={{
            mode: 'javascript',
            theme: 'material'
          }}
        />
      </React.Fragment>
    )
  }
}

ReactDOM.render(<SanityGraphQLDemo />, document.getElementById('demo'))
